body {
  font-family: "Raleway", serif;  
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  overflow-y: auto !important;
}

/* FragmentationImage.module.css */
.fragmentationImage {
  -webkit-mask: paint(fragmentation);
  mask: paint(fragmentation);
  --f-o: 1;
  transition: --f-o 1s ease;
}

.fragmentationImage:hover {
  --f-o: 0;
}

/* Note: This needs to be in a global CSS file (e.g., globals.css) */
@property --f-o {
  syntax: '<number>';
  inherits: false;
  initial-value: 1;
}

/* Locomotive Scroll styles */  
html.has-scroll-smooth {
  overflow: hidden;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

html.has-scroll-dragging {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.has-scroll-smooth body {
  overflow: hidden;
}

[data-scroll-container] {
  min-height: 100vh;
  height: 100%;
  overflow-x: hidden !important;
  overflow: hidden auto;
  position: relative;
  transform-style: preserve-3d;
  perspective: 1px;
  will-change: transform;
  overscroll-behavior: none; /* Prevent bounce effect */
  -webkit-overflow-scrolling: touch;
  overscroll-behavior-y: none;
  scroll-snap-type: y proximity;
}

/* Add smooth scrolling fallback */
@media (prefers-reduced-motion: no-preference) {
  :root {
    scroll-behavior: smooth;
  }
}

/* Swiper custom styles */
.swiper-slide {
  background-position: center;
  background-size: cover;
  filter: blur(4px);
  transition: all 0.3s ease;
  overflow: hidden;
  will-change: transform;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
}

.swiper-slide-active {
  filter: blur(0);
}

.swiper-slide:hover .absolute {
  opacity: 1;
}

.swiper-pagination-bullet {
  background: #2A6177 !important;
}
/* Logo rotation animation */
.logo-container {
  animation: slide 20s linear infinite;
  display: flex;
  width: fit-content;
  margin: 0 auto;
  position: relative;
  will-change: transform;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
}

/* Add these new styles */
.infinite-scroll:hover .logo-container {
  animation-play-state: paused;
}

.logo-group {
  display: flex;
  align-items: center;
  gap: 4rem; /* Consistent gap between logos */
  padding-right: 4rem; /* Space between sets */
  flex-shrink: 0; /* Prevent logo compression */
}

@keyframes slide {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-50%); /* Only move by half to create seamless loop */
  }
}

.infinite-scroll {
  position: relative;
  overflow: hidden;
  width: 100%;
  max-width: 100vw;
  margin: 0 auto;
  padding: 0;
}

/* Remove any potential interference with main layout */
.infinite-scroll * {
  box-sizing: border-box;
}

/* Navigation transition animations */
.nav-expand {
    position: relative;
    z-index: 51;
}

.nav-overlay {
    clip-path: inset(0 100% 0 0);
    animation: slideRight 1s ease-out forwards;
}

@keyframes slideRight {
    0% {
        clip-path: inset(0 100% 0 0);
    }
    100% {
        clip-path: inset(0 0 0 0);
    }
}

/* Navigation styles */
.nav-active {
    opacity: 0.8;
    position: relative;
}

.nav-active::after {
    content: '';
    position: absolute;
    bottom: 40px;
    left: 50%;
    transform: translateX(-50%);
    width: 8px;
    height: 8px;
    background-color: white;
    border-radius: 50%;
}

/* Hide scrollbar but keep functionality */
::-webkit-scrollbar {
  display: none;
}

/* Enable smooth scrolling */
html {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

/* Fix animation performance */
.logo-container, 
.swiper-slide, 
[data-scroll-container] {
  will-change: transform;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
}

/* Improve animation smoothness */
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Prevent horizontal overflow */
.overflow-x-hidden {
  overflow-x: hidden !important;
  width: 100%;
}

/* Smooth transition for all animations */
.transition-all {
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

/* Add these fixes */
.page-transition {
  position: relative;
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

.page-transition-enter {
  opacity: 0;
}

.page-transition-enter-active {
  opacity: 1;
}

.page-transition-exit {
  opacity: 1;
}

.page-transition-exit-active {
  opacity: 0;
}

/* Fix main content layout */
main {
  min-height: 100vh;
  height: fit-content;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
}

/* Fix last section */
main > div:last-child {
  margin-bottom: 0 !important;
  padding-bottom: 0 !important;
  flex-shrink: 0;
}

/* Ensure sections stack properly */
main > div {
  width: 100%;
  position: relative;
}

/* Remove any extra space */
main > div:last-child {
  margin-bottom: 0;
  padding-bottom: 0;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.5;
    transform: scale(0.95);
  }
}

.animate-spin {
  animation: spin 1s linear infinite;
}

.animate-pulse {
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

/* Phone number specific style */
.phone-number {
  font-family: Arial, sans-serif;
}
